import React from 'react';

import Title from '../components/common/title/Title';
import Calculater from '../components/section/calculater/Calculater';
import NewsLetter from '../components/section/NewsLetter';
import StakingProviders from '../components/section/Providers';

function Providers(props) {
    return (
        <>
            <div className="">

                <Title 
                    title={"218Staking Providers We Love."}
                    subTitle={<>
                        <p className='text-white text-center'>Are you looking to stake more than $500k?</p>
                    </>}
                />
                
                <div className="bg-third">
                    <div className="n-container">
                        <StakingProviders />
                    </div>
                </div>

                <div className="bg-primary py-10">
                    <div className="n-container">
                        <Calculater />
                    </div>
                </div>

                <div className="bg-third py-10">
                    <div className="n-container">
                        <NewsLetter />
                        <div className="mt-10">
                            <img src="/img/banner.png" alt="" className="w-full" />
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Providers;
